import React, { Component } from 'react';
import {
    Card,
    CardBody,
    CardHeader,
    Col,
    FormGroup,
    FormText,
    Label,
    Row,
    Button,
    Input
} from 'reactstrap';
import feathers from '../../feathers';
import axios from 'axios';
import immutable from 'object-path-immutable';
import SweetAlert from 'react-bootstrap-sweetalert';
import Select from 'react-select';

class TransferableFixedForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id: props.match.params.id,
            data: [],
            total: 0,
            formData: {
                year: '',
                transmission: '',
                transmissionLow: '',
                distribution: '',
                scxnnomem: '',
                cenace: '',
                cd: [
                    {
                        dd: '',
                        value: ''
                    }
                ]
            },
            zonaDistribucion: [],
            openSuccess: false,
            years: [
                { value: '2018', label: '2018' },
                { value: '2019', label: '2019' },
                { value: '2020', label: '2020' },
                { value: '2021', label: '2021' },
                { value: '2022', label: '2022' },
                { value: '2023', label: '2023' },
                { value: '2024', label: '2024' },
                { value: '2025', label: '2025' }
            ]
        };
    }

    async componentWillMount() {
        console.log('id', this.state.id);
        let _this = this;

        let dds = await feathers.service('distribution-division').find({});
        dds = dds.data.map(dd => {
            return {
                value: dd._id,
                label: dd.distributionDivision
            };
        });

        if (this.state.id != 'form') {
            let findTF = await feathers.service('transferable-fixed').find({
                query: {
                    _id: _this.state.id
                }
            });

            _this.setState({
                formData: {
                    year: findTF.data[0].year || '',
                    transmission: findTF.data[0].transmission || '',
                    transmissionLow: findTF.data[0].transmissionLow || '',
                    distribution: findTF.data[0].distribution || '',
                    scxnnomem: findTF.data[0].scxnnomem || '',
                    cenace: findTF.data[0].cenace || '',
                    cd: findTF.data[0].cd
                },
                zonaDistribucion: dds
            });
        } else {
            _this.setState({
                zonaDistribucion: dds
            });
        }
    }

    onChange = (path, value) => {
        var newFormData = immutable.set(this.state.formData, path, value);

        this.setState({
            formData: newFormData
        });
    };

    onAdd = async formData => {
        let _this = this;
        if (this.form.reportValidity()) {
            if (this.state.id == 'form') {
                let tfResult = await feathers
                    .service('transferable-fixed')
                    .create(_this.state.formData);
                if (tfResult._id != undefined) {
                    window.location.href = '/#/trasladables-fijos';
                }
            } else {
                let tfResult = await feathers
                    .service('transferable-fixed')
                    .patch(_this.state.id, _this.state.formData);
                if (tfResult._id != undefined) {
                    window.location.href = '/#/trasladables-fijos';
                }
            }
        }
    };

    onAddcd = () => {
        var newState = immutable.push(this.state, 'formData.cd', {});
        this.setState(newState);
    };

    removecd = indexContacto => () => {
        var newFormData = immutable.del(this.state.formData, [
            'cd',
            indexContacto
        ]);
        this.setState({
            formData: newFormData
        });
    };

    render() {
        var indexNumber = 0;
        let { formData, openSuccess, years, zonaDistribucion } = this.state;
        const valueFromIdDD = id => zonaDistribucion.find(o => o.value === id);

        const valueFromId = id => years.find(o => o.value === id);

        return (
            <div className="animated fadeIn">
                <form
                    ref={ref => (this.form = ref)}
                    className="form-horizontal"
                >
                    <Row>
                        <Col col="12" sm="12" md="12">
                            <Button
                                onClick={this.onAdd}
                                color="success"
                                className="btn-square pull-right"
                            >
                                <i className="fa fa-save" /> Guardar
                            </Button>
                        </Col>
                    </Row>
                    <br />
                    <Row>
                        <Col xs="12" lg={{ size: 4, offset: 1 }}>
                            <Card>
                                <CardHeader>
                                    Trasladables Fijos
                                    <div className="card-header-actions">
                                        <i className="fa fa-download" />
                                        <a
                                            href="javascript:void(0)"
                                            className="card-header-action"
                                        >
                                            <small className="text-muted">
                                                Alta
                                            </small>
                                        </a>
                                    </div>
                                </CardHeader>
                                <CardBody>
                                    <FormGroup>
                                        <Label htmlFor="hf-year">Año</Label>
                                        <Select
                                            placeholder="-- Seleccionar"
                                            name="year"
                                            value={valueFromId(formData.year)}
                                            options={years}
                                            onChange={event =>
                                                this.onChange(
                                                    'year',
                                                    event.value
                                                )
                                            }
                                        />
                                        <FormText className="help-block">
                                            Seleccione año *campo obligatorio
                                        </FormText>
                                    </FormGroup>
                                    <hr />
                                    <FormGroup>
                                        <Label htmlFor="hf-transmission">
                                            Transmisión (Mayor o igual 220 kV)
                                        </Label>
                                        <Input
                                            type="text"
                                            id="transmission"
                                            name="transmission"
                                            value={formData.transmission}
                                            onChange={event =>
                                                this.onChange(
                                                    'transmission',
                                                    event.target.value
                                                )
                                            }
                                        />
                                        <FormText className="help-block" />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label htmlFor="hf-transmissionLow">
                                            Transmisión ( Menor 220 kV){' '}
                                        </Label>
                                        <Input
                                            type="text"
                                            id="transmissionLow"
                                            name="transmissionLow"
                                            value={formData.transmissionLow}
                                            onChange={event =>
                                                this.onChange(
                                                    'transmissionLow',
                                                    event.target.value
                                                )
                                            }
                                        />
                                        <FormText className="help-block" />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label htmlFor="hf-distribution">
                                            Distribución
                                        </Label>
                                        <Input
                                            type="text"
                                            id="distribution"
                                            value={formData.distribution}
                                            onChange={event =>
                                                this.onChange(
                                                    'distribution',
                                                    event.target.value
                                                )
                                            }
                                        />
                                        <FormText className="help-block" />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label htmlFor="hf-scxnnomem">
                                            SCXN No MEM
                                        </Label>
                                        <Input
                                            type="text"
                                            id="scxnnomem"
                                            value={formData.scxnnomem}
                                            onChange={event =>
                                                this.onChange(
                                                    'scxnnomem',
                                                    event.target.value
                                                )
                                            }
                                        />
                                        <FormText className="help-block" />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label htmlFor="hf-scxnnomem">
                                            CENACE
                                        </Label>
                                        <Input
                                            type="text"
                                            id="cenace"
                                            value={formData.cenace}
                                            onChange={event =>
                                                this.onChange(
                                                    'cenace',
                                                    event.target.value
                                                )
                                            }
                                        />
                                        <FormText className="help-block" />
                                    </FormGroup>
                                </CardBody>
                            </Card>
                        </Col>
                        <Col xs="12" lg={{ size: 6 }}>
                            <Card>
                                <CardHeader>
                                    Información de distribución
                                    <div className="card-header-actions">
                                        <i className="fa fa-download" />
                                        <a
                                            href="javascript:void(0)"
                                            className="card-header-action"
                                        >
                                            <small className="text-muted">
                                                Registrados -{' '}
                                                {formData.cd.length}
                                            </small>
                                        </a>
                                    </div>
                                </CardHeader>
                                <CardBody>
                                    {formData.cd.map((item, index) => (
                                        <Row key={index + 2}>
                                            <Col md={5}>
                                                <FormGroup>
                                                    <Label htmlFor="hf-email">
                                                        División de Distribución
                                                    </Label>
                                                    <Select
                                                        placeholder="-- Seleccione división de distribución"
                                                        name="dd"
                                                        value={valueFromIdDD(
                                                            formData.cd[index]
                                                                .dd
                                                        )}
                                                        options={
                                                            zonaDistribucion
                                                        }
                                                        onChange={event =>
                                                            this.onChange(
                                                                [
                                                                    'cd',
                                                                    index,
                                                                    'dd'
                                                                ],
                                                                event.value
                                                            )
                                                        }
                                                    />
                                                    <FormText className="help-block">
                                                        Campo obligatorio
                                                    </FormText>
                                                </FormGroup>
                                            </Col>
                                            <Col md={5}>
                                                <FormGroup>
                                                    <Label htmlFor="hf-email">
                                                        Valor
                                                    </Label>
                                                    <Input
                                                        type="text"
                                                        id="value"
                                                        value={
                                                            formData.cd[index]
                                                                .value
                                                        }
                                                        onChange={event =>
                                                            this.onChange(
                                                                [
                                                                    'cd',
                                                                    index,
                                                                    'value'
                                                                ],
                                                                event.target
                                                                    .value
                                                            )
                                                        }
                                                    />
                                                </FormGroup>
                                            </Col>

                                            {formData.cd.length > index && (
                                                <Col md={1}>
                                                    <label htmlFor="addCD">
                                                        &nbsp;
                                                    </label>
                                                    <div className="form-group">
                                                        <Button
                                                            color="danger"
                                                            onClick={this.removecd(
                                                                index
                                                            )}
                                                        >
                                                            -
                                                        </Button>
                                                    </div>
                                                </Col>
                                            )}
                                            {formData.cd.length - 1 ==
                                                index && (
                                                <Col md={1}>
                                                    <label htmlFor="addCD">
                                                        &nbsp;
                                                    </label>
                                                    <div className="form-group">
                                                        <Button
                                                            color="primary"
                                                            onClick={
                                                                this.onAddcd
                                                            }
                                                        >
                                                            +
                                                        </Button>
                                                    </div>
                                                </Col>
                                            )}
                                        </Row>
                                    ))}
                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </form>
            </div>
        );
    }
}

export default TransferableFixedForm;
